import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { filter } from 'rxjs/operators';
import { CONFIG } from '../config';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'ibss-sidebars',
  templateUrl: './sidebars.component.html',
  styleUrls: ['./sidebars.component.scss']
})
export class SidebarsComponent implements OnInit {

  currentRoute: string = "";
  canShowMenu = true;
  config:typeof CONFIG = CONFIG;
  
  constructor(
    private router: Router,
    private authService: AuthService) {
    
  }
  
  ngOnInit() {
    this.currentRoute = this.router.url;
    this.checkUrl();
    this.router.events.pipe(
      filter((event: any) => event instanceof NavigationEnd)
    )
    .subscribe((event: any) => {
      this.currentRoute = event.url;
      this.checkUrl();
    }); 
  }

  checkUrl() {
    this.canShowMenu = ! (this.currentRoute.toLowerCase().includes('create') || this.currentRoute.toLowerCase().includes('edit'));

    this.config.isMenuVisible =  this.canShowMenu;
  }

  onLogout() {
    this.authService.logout();
  }
}
