import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home.component';

const routes: Routes = [
  { 
    path: '',
    component: HomeComponent,
    children: [
      { path: '', redirectTo: "dashboard"},
      { path: 'dashboard', loadChildren: () => import('../dashboard/dashboard.module').then(m => m.DashboardModule)},
      { path: 'sample', loadChildren: () => import('../sample/sample.module').then(m => m.SampleModule)},
      { path: 'profile', loadChildren: () => import('../profile/profile.module').then(m => m.ProfileModule) },
      { path: 'create', loadChildren: () => import('../wizard/wizard.module').then(m => m.WizardModule) },
      { path: 'edit/:Id', loadChildren: () => import('../wizard/wizard.module').then(m => m.WizardModule) },
      { path: 'messages/:Id', loadChildren: () => import('../messages/messages.module').then(m => m.MessagesModule) }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRoutingModule { }
