import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { CONFIG } from '../config';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  encapsulation:ViewEncapsulation.None
})
export class HomeComponent implements OnInit {
  config: typeof CONFIG = CONFIG;
  userName = "";
  userEmail = "";
  constructor() { }

  ngOnInit(): void {
    console.log(this.config.user);
    console.log(this.config.login);

    if (this.config.user){
      this.userName = this.config.user.firstName + " " +(this.config.user.middleName && this.config.user.middleName.trim().length>0 ? (this.config.user.middleName + " "): "") + this.config.user.lastName;
      this.userEmail = this.config.login && this.config.login.email;
    }
  }

}
